import React from "react"
import Anime from 'react-anime'
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import gif1 from  "../images/fullstackdev/fullstackdev_addinginstructor.gif"
import gif2 from  "../images/fullstackdev/fullstackdev_viewingcurriculum.gif"
import 'bootstrap/dist/css/bootstrap.css';
import { Container, Row, Col } from "react-bootstrap"
import "../components/app.css"
import Layout from "../components/layout.js"
import SEO from "../components/seo"
import ProjectNav from "../components/project-nav"

const FullStackDevPage = () => {
  const data = useStaticQuery(graphql`
    query {
      imageOne: file(relativePath: { eq: "home/272.png" }) {
        childImageSharp {
          fluid(maxWidth: 1000) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      imageTwo: file(relativePath: { eq: "fullstackdev/fullstackdev_camp.png" }) {
        childImageSharp {
          fluid(maxWidth: 1000) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      imageThree: file(relativePath: { eq: "fullstackdev/fullstackdev_curriculum.png" }) {
        childImageSharp {
          fluid(maxWidth: 1000) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      imageFour: file(relativePath: { eq: "fullstackdev/fullstackdev_instructorprofile.png" }) {
        childImageSharp {
          fluid(maxWidth: 1000) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)
  return (
  <Layout>
    <Container fluid={true}>
      <Row>
        <ProjectNav 
          title="Chess Camps — Full Stack Development Page"
          info="Spring 2018 | Carnegie Mellon University, Pittsurgh PA"
          skills="Frontend development, Backend development, Ruby on Rails, VueJS, Information design"
          left="/community/"
          right="/flexable/"
        >
        </ProjectNav>
        <Anime opacity={[0, 1]} translateY={'1em'} delay={(e, i) => 250}>
        <Col xs={12} sm={12} md={12} lg={{span:8, offset:4}} xl={{span:8, offset:4}} className="project-content">
          <Img fluid={data.imageOne.childImageSharp.fluid} alt="project cover"/>
          <br /><br />
          <h2 className="project-h2">Background</h2>
          <p>During the <a href="https://67272.cmuis.net/" target="_blank" className="link">67-272 Application Design and Development</a> course at Carnegie Mellon University, students work on a semester-long project which involves building a Ruby on Rails website for a business (business concept created by the professor). During my semester, I built a Ruby On Rails website for a chess camp business, where parents/guardians can sign their children up for chess camps. I created the website that can be used by three users: administrators, chess instructors and parents/guardians. While one of the main objectives of this course was to create a fully functioning web application using Ruby on Rails and VueJS, we also focused on user-centered design and information hierarchy. Students had to freedom to design the frontend of their individual websites. Shown below are some screenshots and GIFs of my website:</p>
          <br /><br />

          <h2 className="project-h2">Screenshots</h2>
          <Img fluid={data.imageTwo.childImageSharp.fluid} alt="camp detail page screenshot"/><br /><br />
          <Img fluid={data.imageThree.childImageSharp.fluid} alt="curriculum detail page screenshot"/><br /><br />
          <Img fluid={data.imageFour.childImageSharp.fluid} alt="user profile page screenshot"/><br />

          <br /><br />
          <h2 className="project-h2">Interaction GIFs</h2>
          <p><img src={gif1} alt="" className="project-gif"/></p><br />
          <img src={gif2} alt="" className="project-gif"/><br />
        </Col>
        </Anime>
      </Row>
    </Container>
  </Layout>
  )
}

export default FullStackDevPage
